                  </template>
                  <span>
                    QÔºö‰∏∫‰ªÄ‰πàË¶ÅËæìÂÖ•uidÔºü<br />
                    AÔºöÂõ†‰∏∫Ë¶ÅÁî®Êù•Ëé∑Âèñ‰Ω†ÁöÑËøΩÁï™ÂàóË°®Ôºå‰ª•ÂÖçÂú®‰Ω†ÁöÑÊó•ÂéÜÈáåÊòæÁ§∫‰Ω†‰∏çÁúãÁöÑÁï™<br />
                    <br />
                    QÔºöbÁ´ôuidÂú®Âì™ÊâæÔºü<br />
                    appÔºö„ÄêÊàëÁöÑ„Äë-„ÄêÁ©∫Èó¥„Äë-„ÄêÁºñËæëËµÑÊñô„Äë<br />
                    ÁΩëÈ°µÔºöÂèØ‰ª•Âú®„Äê‰∏™‰∫∫Á©∫Èó¥„ÄëÂè≥‰æßÊ†è‰∏ãÈù¢ÊâæÂà∞<br />
                    <br />
                    QÔºöÊèêÁ§∫‚ÄúÁî®Êà∑ÈöêÁßÅËÆæÁΩÆÊú™ÂÖ¨ÂºÄ‚ÄùÊÄé‰πàÂäûÔºü<br />
                    appÔºö„ÄêÊàëÁöÑ„Äë- „ÄêÁ©∫Èó¥„Äë- Âè≥‰∏äËßí‰∏â‰∏™ÁÇπ - „ÄêÁ©∫Èó¥ËÆæÁΩÆ„Äë-
                    ÂºÄÂêØ„ÄêÂÖ¨ÂºÄÊòæÁ§∫ËÆ¢ÈòÖÁöÑÁï™Ââß„Äë<br />
                    ÁΩëÈ°µÔºö„Äê‰∏™‰∫∫Á©∫Èó¥„Äë-„ÄêËÆæÁΩÆ„Äë-„ÄêÈöêÁßÅËÆæÁΩÆ„Äë- ÂºÄÂêØ„ÄêËøΩÁï™ËøΩÂâß„Äë
