<script>
  export default {
    name: "App",

    data: () => ({
      pageTitle: "bilibiliËøΩÁï™Êó•ÂéÜ by hi94740",
      buid: "",
      fakeLoading: false,
      buidStats: null,
      loadingBuidStats: false,
      errorTitle: "Âá∫Èîô‰∫ÜQAQ",
      errorMsg: "È™ó‰Ω†ÁöÑÂòøÂòøüòã",
      errorIcon: "mdi-sync-alert",
      showError: false,
      showQA: false
    }),

    computed: {
      isBUIDvalid() {
        return /^[1-9]\d*$/.test(this.buid) && parseInt(this.buid) > 0
      },
      cantFetchCalendar() {
        if (this.buidStats) {
          if (this.buidStats.userInfo.code != 0)
            return this.buidStats.userInfo.message
          else if (this.buidStats.bangumiFollow.code != 0)
            return this.buidStats.bangumiFollow.message
          else if (this.buidStats.bangumiFollow.data.list.length === 0)
            return "‰Ω†ËøòÊ≤°ËÆ¢ÈòÖÁï™ÂâßÂë¢ÔºÅ"
          else return false
        } else return true
      },
      inputBuid: {
        get() {
          return this.cantFetchCalendar
            ? this.buid
            : this.buidStats.userInfo.data.name
        },
        set(buid) {
          this.buid = buid
        }
      }
    },

    watch: {
      buid(buid) {
        this.$nextTick(() => {
          if (typeof buid === "string") {
            buid = buid.toLowerCase()
            if (buid.startsWith("uid:")) this.buid = buid.replace("uid:", "")
            const int = parseInt(this.buid)
            this.buid = !isNaN(int) ? (Math.abs(int) || "") + "" : ""
          }
        })
      }
    },

    methods: {
      buidInputErrorMsg(buid) {
        return this.buidStats?.buid == buid
          ? typeof this.cantFetchCalendar == "string"
            ? this.cantFetchCalendar
            : true
          : this.buid == "" ||
              this.buid == null ||
              this.isBUIDvalid ||
              "uidÂøÖÈ°ªÊòØÊï∞Â≠óÔºÅ"
      },
      fakeLoad() {
        this.fakeLoading = true
        setTimeout(() => (this.fakeLoading = false), 2000)
      },
      async loadBuidStats() {
        if (this.isBUIDvalid) {
          this.loadingBuidStats = true
          const buid = this.buid
          try {
            const res = await fetch(
              process.env.VUE_APP_HOST + "bilibili/uid/validate?uid=" + buid
            )
            if (!res.ok) {
              this.errorMsg = res.status + "Ôºö" + res.statusText
              this.showError = true
            }
            const stats = await res.json()
            stats.buid = buid
            this.buidStats = stats
          } catch (e) {
            console.error(e)
            this.errorMsg = e.toString()
            this.showError = true
          }
          this.buid = ""
          this.$nextTick(() => (this.buid = buid))
          this.loadingBuidStats = false
        }
      },
      clearStats() {
        this.buid = ""
        this.buidStats = null
      }
    }
  }
</script>
